<script>
// import image from '../@/assets/'
// import $ from 'jquery'
import hamburger from '@/components/hamburger.vue'
// import '../assets/css/phone.css'
import $ from 'jquery'
import Vue from 'vue'
import VuePageTitle from 'vue-page-title'
import 'vue-multi-ref'
import VueLazyload from 'vue-lazyload'
var VueScrollTo = require('vue-scrollto');
Vue.use(VueLazyload)
Vue.use(VueScrollTo)
Vue.use(VuePageTitle, {
  // prefix: 'My App - ',
  // suffix: '歷史軌跡'
})

Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: 'dist/error.png',
  loading: 'dist/loading.gif',
  attempt: 1
})

window.Vue = require('vue')
require('vue-multi-ref')

export default {
  title:"關於嘉義",
  name: 'chiayi',
  components: {
    hamburger
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
        teacher_hide:false,
        dialog_hide:false,
        time_of_scroll:0,
        current_position : -1,
        up:false,
        t:0,
        p:0,
        activebr:'block'
    }
  },
  mounted(){
      if(window.innerHeight < window.innerWidth){
        for(let i=0;i<this.$refs.story_bg.length;i++){
            this.$refs.story_bg[i].style.height = window.innerHeight + "px"
        }
      }else{
        this.activebr = 'none'
      }
    },
  methods:{
      scroll(event){
        //   console.log("scroll")
        //   console.log(this.$refs.top_content.getBoundingClientRect().top)
        //   console.log(this.$refs.top_content.scrollTop)
        //   console.log(window.scrollY)
