              this.teacher_hide = true
              this.dialog_hide = true
          }
        //   console.log("window_scroll" + window.scrollY)
        //   console.log(window.innerHeight + window.scrollY)
        //   console.log()
        //   console.log(document.body.offsetHeight - 10)
          if ((window.innerHeight + window.scrollY) > document.body.offsetHeight - 50) {
            //   console.log("XXX");
          }
            console.log("scroll")
            this.p=document.documentElement.scrollTop
            console.log("p"+this.p)
            console.log("t"+this.t)
            if(this.t<this.p){
                this.up = false
            }else{
                this.up = true
            }
            this.t = this.p;

            let s = document.getElementsByClassName("block");
            console.log(this.up)
            if(window.innerHeight < window.innerWidth){
                if($(".block").eq(this.current_position + 1).offset().top < document.documentElement.scrollTop + (window.innerHeight/2) &&
                    $(".block").eq(this.current_position + 1).offset().top > document.documentElement.scrollTop + 20 && this.up ==false && this.current_position < 11){
                        console.log($(".block").eq(this.current_position + 1).offset().top)
                        console.log(document.documentElement.scrollTop + 80)
                        console.log(document.documentElement.scrollTop +20)
                        console.log(this.current_position + 1)
                       $('html, body').animate({
                        scrollTop: $(".block").eq(this.current_position + 1).offset().top
                        });
                        this.current_position++
                        if(this.current_position > s.length-2){
                            this.current_position = 11
                        }
                }
                // else if($(".block").eq(this.current_position - 1).offset().top + window.innerHeight + (window.innerHeight/4) < document.documentElement.scrollTop && this.up ==true){
                //         $('html, body').animate({
                //             scrollTop: $(".block").eq(this.current_position - 1).offset().top
                //         });
                //         this.current_position--
                //         if(this.current_position < 1){
                //             this.current_position = 1
                //         }
                // }
            }

      },

  },
  created(){
      window.addEventListener('scroll',this.scroll);
  },
}



