        /* opacity: .8; */
    }

    div.dialog_shape_right::after{
        content:"";
        border-left:10px solid transparent;
        border-right: -66px solid transparent;
        border-top: 30px solid #FFFFFF;
        transform: rotate(237deg);
        position: absolute;
        top:15%;
        right:2%;
    }

    div.dialog_shape_left::before{
        content:"";
        border-left:10px solid transparent;
        border-right: 10px solid transparent;
        border-top: 62px solid #FFFFFF;
        position: absolute;
        transform: rotate(55deg);
        top:30%;
        left:0.5%;
    }

    .content p{
        color:#55707d;
    }


    img.character{
        width:60%;
        transform:rotate(0deg);
    }



    img.student{
        width: 80%;
    }

    img.teacher_turkey{
        width: 90% !important;
    }

    img.handsup{
        width: 90% !important;
    }

    img.cross_turkey{
        width: 90% !important;
    }

    img.turkey_fly{
        width:80%;
    }



    img.question{
        width: 70%;
    }

    img.turkey_fly{
        width:80%;
    }

     img.turkey_fly_stand{
        width:60%;
    }


    img.turkey_fly_no_id_picture{
        /* width: ; */
    }

    img.id_picture{
        width:55%;
        /* height: 60%; */
        display:block;
        margin:auto;
        margin-top:30px;
    }

    .identification img.character{
        width:100%;
        margin-top:-100% ;
    }

    .identification img.turkey_fly{
        width:130%;
    }

    .identification img.stick_turkey{
        /* margin-top: -250%;
        margin-left: 80%; */
        width: 70%;
        margin-top: -60%;
    }

    .identification img.stick{
        margin-top: -200%;
        margin-left: 60%;
        width: 60%;
    }



    .identification div.dialog_shape_left::before{
        top:50%;
    }

    .identification div.dialog p{
        font-size: 1.2vw;
        margin-bottom: 0;
    }

    .chiayi_station{
        background-image:url("../assets/picture/1/資產6.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding: 0;
    }

    .train_mountain{
        background-image:url("../assets/picture/2/資產10.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding: 0;
    }

    .app{
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .app_logo{
        width: 20%;
        float:right;
        margin-right:2%;
        margin-top: 2%;
    }

    .app .dialog_shape_left{
        margin-top: -10%;
    }

    .app .dialog_shape_left::before{
        margin-top: -5%;
    }

    .tw_map{
        background-image:url("../assets/picture/4/資產19.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .village{
        background-image:url("../assets/picture/5/資產21.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .dutch{
        background-image:url("../assets/picture/6/資產25.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .cheng{
        background-image:url("../assets/picture/7/資產29.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .mountain{
        background-image:url("../assets/picture/8/資產33.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .mountain_to_chiayi{
        /* background-image:url("../assets/picture/9/資產51.png"); */
        /* background-repeat: no-repeat; */
        /* background-size: cover; */
        padding:0;
    }

    .lin_event{
        background-color: #fff;
        background-image:url("../assets/picture/10/資產-46.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .txt{
        background-image:url("../assets/picture/11/資產43.png");
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .interest{
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .temple{
        background-repeat: no-repeat;
        background-size: cover;
        padding:0;
    }

    .block{
        padding-right: 5vw;
        padding-left: 5vw;
    }

    @media(max-width:410px){
        /* div.dialog{
            font-size: 20px;
        } */
        .app_logo{
            display:none;
        }

        div.block{
            background-image:none;
            background:none;
            padding:0;
        }

        div.dialog p,.identification div.dialog p{
            font-size: 4.7vw;
        }

         div.dialog_shape_right::after{
            /* top: 40%; */
            right: 2%;
        }

        div.dialog_shape_left::before{
            /* margin-left:-2%; */
            top: 23%;
            left: 5%;
        }

        .identification img.character{
            width: 100%;
            margin-top: 0%;
        }

        .identification img.turkey_fly{
            /* width:80%; */
        }

        .identification img.turkey_standing{
            width: 80%;
        }

        .identification img.stick{
            margin-top: -300%;
        }

        /* .identification div.dialog p{
            font-size: 4vw;
        } */

        img.id_picture{
            width: 80%;
        }

        .no-padding{
            padding: 0;
        }

        .gogo{
            margin-top: 40px;
            margin-left: 30%;
            /* animation-name:go; */
            animation-name:gogo;
            animation-duration:.5s;
            animation-iteration-count: infinite;
            animation-direction: alternate;
            /* font-size: 20px; */
            /* animation: go .5s linear .1s infinite alternate; */
        }


        @keyframes gogo {
            from{
                font-size: 15px;
            }
            to{
                font-size:20px;
            }
        }


    }
</style>



